.pagination_box {
    display: flex;
    justify-content: center;
    flex-direction: column;
    max-width: 1200px;
    margin: 0 auto;
    padding: 1% 0;
}

.pagination_box ul {
    display: flex;
    justify-content: center;
}

.pagination_box li {
    display: flex;
    justify-content: center;
    font-size: 15px;
    /* min-width: 30px; */
    width: auto;
    border-radius: 50%;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.068);
    padding: 0 5px;
    margin: 0 1%;
}

.pagination_box li:hover {
    color: blue;
    transform: scale(1.2);
    transition: 0.2s linear;
}
