.logoIMG {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s linear;
    font-size: 25px;
    text-transform: uppercase;
    opacity: 0.9999;
    font-weight: 300;
    font-family: 'Open Sans', sans-serif;
    text-decoration: none;
    color: white;
    margin-bottom: 0.5rem;
}
.logoIMG:hover {
    text-decoration: none;
    text-shadow: 0 1px 0 rgb(255 255 255 / 10%), 0 0 15px rgb(255 255 255 / 74%);
}

@media (min-width: 1200px) {
    .logoIMG {
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s linear;
        font-size: 25px;
        text-transform: uppercase;
        opacity: 0.9999;
        font-weight: 300;
        font-family: 'Open Sans', sans-serif;
        text-decoration: none;
        color: white;
        margin: 0;
    }
}
