.containerSlider {
    max-width: 1100px;
    margin: 0 auto;
}

.containerSlider img {
    width: 270px;
    border-radius: 2%;
}
.containerSlider img:hover {
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.7);
    cursor: pointer;
}

.containerSlider button {
    display: none !important;
}

@media (max-width: 1200px) {
    .containerSlider {
        display: none;
    }
}

.mainTrending {
    display: flex;
    justify-content: center;
    flex-direction: column;
    /* background-color: rgb(237, 243, 178);
    background: #ffffff; */
    max-width: 1200px;
    margin: 0 auto;
    /* border: 2px solid gray; */
    height: 100%;
}

.itemsWrap {
    padding: 0;
    margin: 1rem auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

/* .items li { */
.mainLI {
    display: inline-block;
    /* background-color: rgba(245, 245, 245, 0.621); */
    /* border: 2px solid gray; */
    border-radius: 3%;
    margin: 1%;
    padding-bottom: 1%;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.7);
}
.mainLI:hover {
    transform: scale(1.03);
    transition: 0.5s linear;
}

.containerIMG {
    width: 95%;
    position: relative;
    /* max-width: 200px; */
    display: block;
    justify-items: center;
    min-height: 310px;
    margin: 0.3rem auto;
    background-color: transparent;
}

.items img {
    width: 100%;
    max-width: 200px;
    margin: 0;
    background-color: transparent;
}

.items p {
    margin: 3% 0 0 2%;
    margin-top: 1;
}

.title {
    font-family: 'PF DinDisplay Medium', 'Trebuchet MS', Arial;
    font-weight: 600;
    line-height: 18px;
    font-size: 13px;
}

.title a {
    color: rgb(66, 64, 64);
    text-decoration: none;
}

.title :hover {
    text-decoration: underline;
}

.load_more {
    display: grid;
    justify-content: center;
    padding: 2%;
}
.svgBtn {
    position: absolute;
    top: 5%;
    left: 5%;
    z-index: 3;
    transition: 0.5s;
    width: 2rem;
    height: 2rem;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
    opacity: 0;
}
.queueBTN {
    fill: currentColor;
    width: 1em;
    height: 1em;
    display: inline-block;
    font-size: 1.5rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    user-select: none;
}

.mainLI:hover .svgBtn {
    transform: scale(1.03);
    transition: 0.5s linear;
    opacity: 1;
    cursor: pointer;
    color: rgba(35, 226, 115, 0.664);
    background-color: rgba(185, 185, 182, 0.596);
}
